/* Vendor Order Details - Bill/Invoice Style */

.orderDetailsContainer {
  padding: 24px;
  max-width: 900px;
  margin: 0 auto;
  background: var(--bg-main);
  min-height: 100vh;
}

.orderDetailsHeader {
  margin-bottom: 24px;
}

.backButton {
  background: transparent;
  border: 1px solid var(--border);
  color: var(--text-secondary);
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.backButton:hover {
  background: var(--bg-hover);
  color: var(--text-primary);
  border-color: var(--text-secondary);
}

.loadingState,
.errorState {
  text-align: center;
  padding: 48px 24px;
  color: var(--text-secondary);
  font-size: 16px;
}

/* Bill Card */
.orderBillCard {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

/* Bill Header */
.billHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 32px 24px;
  border-bottom: 2px solid var(--divider);
}

.billTitleSection {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.billTitle {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
}

.orderIdDisplay {
  font-size: 16px;
  color: var(--text-secondary);
  font-weight: 500;
  font-family: 'Courier New', monospace;
}

.orderStatusBadge {
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  text-transform: capitalize;
}

.orderStatusBadge.pending {
  background: #FFF3E0;
  color: #E65100;
}

.orderStatusBadge.processing {
  background: #E3F2FD;
  color: #1565C0;
}

.orderStatusBadge.completed {
  background: #E8F5E9;
  color: #2E7D32;
}

.orderStatusBadge.cancelled {
  background: #FFEBEE;
  color: #C62828;
}

/* Bill Sections */
.billSection {
  padding: 24px 32px;
  border-bottom: 1px solid var(--divider);
}

.billSection:last-child {
  border-bottom: none;
}

.billSectionTitle {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 16px 0;
}

/* Bill Info Grid */
.billInfoGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
}

.billInfoItem {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.billInfoItem.fullWidth {
  grid-column: 1 / -1;
}

.billInfoLabel {
  font-size: 13px;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.billInfoValue {
  font-size: 15px;
  color: var(--text-primary);
  font-weight: 500;
}

/* Bill Table */
.billTableWrapper {
  overflow-x: auto;
  margin-top: 8px;
}

.billTable {
  width: 100%;
  border-collapse: collapse;
  font-size: 15px;
}

.billTable thead th {
  background: var(--bg-hover);
  color: var(--text-secondary);
  font-weight: 600;
  text-align: left;
  padding: 12px 16px;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 2px solid var(--divider);
}

.billTable tbody tr {
  border-bottom: 1px solid var(--divider);
}

.billTable tbody tr:last-child {
  border-bottom: none;
}

.billTable tbody td {
  padding: 16px;
  color: var(--text-primary);
}

.itemNameCol {
  width: 50%;
}

.itemPriceCol,
.itemQtyCol {
  width: 15%;
  text-align: right;
}

.itemTotalCol {
  width: 20%;
  text-align: right;
}

.itemNameCell {
  font-weight: 500;
}

.itemPriceCell,
.itemQtyCell,
.itemTotalCell {
  text-align: right;
  font-family: 'Courier New', monospace;
}

.itemTotalCell {
  font-weight: 600;
}

/* Bill Summary */
.billSummary {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 400px;
  margin-left: auto;
}

.billSummaryRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
}

.billSummaryLabel {
  color: var(--text-secondary);
  font-weight: 500;
}

.billSummaryValue {
  color: var(--text-primary);
  font-weight: 600;
  font-family: 'Courier New', monospace;
}

.billSummaryRow.billTotal {
  margin-top: 8px;
  padding-top: 12px;
  border-top: 2px solid var(--divider);
  font-size: 18px;
}

.billSummaryRow.billTotal .billSummaryLabel {
  color: var(--text-primary);
  font-weight: 700;
}

.billSummaryRow.billTotal .billSummaryValue {
  color: var(--primary);
  font-weight: 700;
  font-size: 20px;
}

/* Bill Notes */
.billNotes {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.billNoteItem {
  padding: 12px 16px;
  background: var(--bg-hover);
  border-radius: 6px;
  font-size: 14px;
  color: var(--text-primary);
  line-height: 1.6;
}

.billNoteItem strong {
  color: var(--text-secondary);
  font-weight: 600;
  display: block;
  margin-bottom: 4px;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .orderDetailsContainer {
    padding: 16px;
  }

  .billHeader {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    padding: 24px 20px;
  }

  .billTitle {
    font-size: 24px;
  }

  .billSection {
    padding: 20px;
  }

  .billSectionTitle {
    font-size: 16px;
  }

  .billInfoGrid {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .billTable {
    font-size: 14px;
  }

  .billTable thead th {
    padding: 10px 12px;
    font-size: 12px;
  }

  .billTable tbody td {
    padding: 12px;
  }

  .itemNameCol {
    width: 40%;
  }

  .itemPriceCol,
  .itemQtyCol,
  .itemTotalCol {
    width: 20%;
  }

  .billSummary {
    max-width: 100%;
  }

  .billSummaryRow {
    font-size: 14px;
  }

  .billSummaryRow.billTotal {
    font-size: 16px;
  }

  .billSummaryRow.billTotal .billSummaryValue {
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .billTable thead th,
  .billTable tbody td {
    padding: 8px;
  }

  .billTable {
    font-size: 13px;
  }

  .billTable thead th {
    font-size: 11px;
  }

  .itemNameCol {
    width: 35%;
  }

  .itemPriceCol {
    width: 20%;
  }

  .itemQtyCol {
    width: 15%;
  }

  .itemTotalCol {
    width: 25%;
  }
}

/* Dark theme adjustments */
body.theme-dark .orderStatusBadge.pending {
  background: #4A3000;
  color: #FFB74D;
}

body.theme-dark .orderStatusBadge.processing {
  background: #003D5C;
  color: #4FC3F7;
}

body.theme-dark .orderStatusBadge.completed {
  background: #1B3A1F;
  color: #66BB6A;
}

body.theme-dark .orderStatusBadge.cancelled {
  background: #4A1416;
  color: #EF5350;
}
